import MiniGL from "../MiniGL";

class Texture {
    /**
     * @param {Boolean} truergb*aalpha
     */
    premultiplyAlpha = true;
    miniGL: MiniGL;
    canvas: HTMLCanvasElement;
    ctx: CanvasRenderingContext2D;
    webglTexture: WebGLTexture;
    constructor(miniGL: MiniGL) {
        this.miniGL = miniGL;
        this.canvas = document.createElement("canvas");
        this.ctx = this.canvas.getContext("2d");
    }
    lg2(n) {
        return Math.log(n) / Math.log(2);
    }
    create({
        image,
        rect,
        reverseY = true,
        name,
    }: {
        image: HTMLCanvasElement;
        rect: any;
        reverseY: boolean;
        name: string;
    }) {
        if (rect) {
            this.canvas.width = rect.width;
            this.canvas.height = rect.height;
            this.canvas.setAttribute("data-name", name);
            this.ctx.drawImage(image, -rect.x, -rect.y);
            image = this.canvas;
        }
        const gl = this.miniGL.gl;
        const texture = gl.createTexture();

        // 
        gl.bindTexture(gl.TEXTURE_2D, texture);
        // 
        gl.pixelStorei(
            gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL,
            this.premultiplyAlpha
        );
        // 
        gl.texImage2D(
            gl.TEXTURE_2D,
            0,
            gl.RGBA,
            gl.RGBA,
            gl.UNSIGNED_BYTE,
            image
        );
        // y
        if (reverseY) {
            gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, reverseY);
        }

        // 
        gl.generateMipmap(gl.TEXTURE_2D);

        // 
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
        gl.texParameteri(
            gl.TEXTURE_2D,
            gl.TEXTURE_MIN_FILTER,
            gl.LINEAR_MIPMAP_LINEAR
        );
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
        gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);

        // 
        gl.bindTexture(gl.TEXTURE_2D, null);
        this.webglTexture = texture;
        return texture;
    }

    /**
     * 
     * @param {*} texture
     * @param {*} image
     */
    update(texture: WebGLTexture, image: HTMLImageElement) {
        const gl = this.miniGL.gl;
        gl.bindTexture(gl.TEXTURE_2D, texture);
        gl.texImage2D(
            gl.TEXTURE_2D,
            0,
            gl.RGBA,
            gl.RGBA,
            gl.UNSIGNED_BYTE,
            image
        );
    }
}
export default Texture;
